import React, { Component } from 'react';
import LEdit from 'wangeditor';

export default class Child extends Component {
    constructor(props) {
        super(props);
        this.state = {
            editorContent:''
         };
         this.textAreaValue=this.textAreaValue.bind(this);
    }
    componentDidMount() {
        const elemMenu = this.refs.editorElemMenu;
        const elemBody = this.refs.editorElemBody;
        const editor = new LEdit(elemMenu, elemBody)
        //  onchange  state 
        editor.customConfig.onchange = html => {
            // console.log(editor.txt.html())
            this.setState({
                // editorContent: editor.txt.text()
                editorContent: editor.txt.html()
            })
        }
        editor.customConfig.menus = [
            'head',  // 
            'bold',  // 
            'fontSize',  // 
            'fontName',  // 
            'italic',  // 
            'underline',  // 
            'strikeThrough',  // 
            'foreColor',  // 
            'backColor',  // 
            'link',  // 
            'list',  // 
            'justify',  // 
            'quote',  // 
            'emoticon',  // 
            'image',  // 
            'table',  // 
            'video',  // 
            'code',  // 
            'undo',  // 
            'redo'  // 
        ]
        editor.customConfig.uploadImgShowBase64 = true
        editor.create()
    };
    textAreaValue () {
        console.log('data', this.state.editorContent)
    }
    render() {
        return (
            <div className="shop">
                <div className="text-area" >
                    <div ref="editorElemMenu"
                         style={{backgroundColor:'#f1f1f1',border:"1px solid #ccc",color: 'dodgerblue'}}
                         className="editorElem-menu">
                    </div>
                    <div
                        style={{
                            padding:"0 10px",
                            overflowY:"scroll",
                            height:300,
                            border:"1px solid #ccc",
                            borderTop:"none"
                        }}
                        ref="editorElemBody" className="editorElem-body">
                    </div>
                </div>
            </div>
        )
    }
}
