import React,{ Component } from 'react';
import {connect} from 'react-redux';
import Child from '../../../../common/components/Child';
import {Radio} from 'antd';
import 'antd/es/radio/style/index.css';
import {actionCreators} from '../../store/index';
import {
	ArticleWrapper
} from './style.js';

class Article extends Component{
	render(){
		return(
			<ArticleWrapper>
				<label className="WriteIndex-titleInput Input-wrapper Input-wrapper--multiline">
				<textarea ref={(textarea)=>{this.title=textarea}} rows="1" maxLength='20' required className="Input" placeholder=" 20 " style={{height: '45px'}}>
				</textarea>
				</label>
				<Child ref="childElem"/>
				<div style={{marginTop: '20px'}}>
					<br/><br/>					
					<Radio.Group ref="radioElem" defaultValue="1" buttonStyle="solid">
						<Radio.Button style={{margin:'3px'}} value="1"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="2"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="3"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="4"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="5"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="6"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="7"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="8"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="9"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="10"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="11"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="12"></Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="13"></Radio.Button>
					</Radio.Group>                        
				</div>				
				<button style={{marginTop: '60px',marginBottom:'20px'}} onClick={()=>this.props.addArticle(this.title,this.refs.childElem,this.refs.radioElem)} className="ReleaseButton"></button>
			</ArticleWrapper>				
		)
	}
}
const mapDispatch=(dispatch)=>({
	addArticle(title,data,tid){
		const text=data.state.editorContent;
		if((title.value=== '' ||title.value === null)||(text=== '' ||text === null)){
			alert("");
		}else{
			const date=new Date();
			const date1=date.getFullYear() + '-' + (date.getMonth()+1) + '-' + date.getDate();
			dispatch(actionCreators.addArticle(title.value,text,date1,tid.state.value));
		}		
	}
})
export default connect(null,mapDispatch)(Article);